<template>	
	<div class='ratings' ref='ratingscroll'>
		<div class='ratings-content'>
			<div class='overview'>
				<div class='overview-left'>
					<h1 class='score'>{{ seller.score }}</h1>
					<div class='title'>综合评分</div>
					<div class='rank'>高于周边商家{{ seller.rankRate }}</div>
				</div>
				<div class='overview-right'>
					<div class='score-wrapper'>
						<span class='title'>服务态度</span>
						<star :size='36' :score='seller.serviceScore'></star>
						<span class='score'>{{ seller.serviceScore }}</span>
					</div>
					<div class='score-wrapper'>
						<span class='title'>商品评分</span>
						<star :size='36' :score='seller.foodScore'></star>
						<span class='score'>{{ seller.foodScore }}</span>
					</div>
					<div class="deliveryWrapper">
						<span class="title">送达时间</span>
						<span class="deliverytime">{{seller.deliveryTime}}分钟</span>
					</div>
				</div>
			</div>
			<split></split>
			<ratingselect :select-type='selectType' :desc='desc' :ratings='ratings'></ratingselect>

			<div class="rating-wrapper">
				<ul>
					<li class="rating-item" v-for="rating in ratings" v-show='needShow(rating.rateType,rating.text)'>
						<div class="avatar">
							<img :src="rating.avatar" width="28" height="28">
						</div>
						<div class="content">
								<h1 class="name">{{rating.username}}</h1>
								<div class="star-wrapper">
									<star :size="24" :score="rating.score"></star>
									<span class="delivery" v-show='rating.deliveryTime'>{{rating.deliveryTime}}分钟送达</span>
								</div>
								<p class="text">{{rating.text}}</p>
								<div class="recommend" v-show="rating.recommend.length">
									<span class="icon-thumb_up" ></span>
									<span class="item" v-for="item in rating.recommend">{{item}}</span>
								</div>
								<div class='time'>{{ DateFormat(new Date(rating.rateTime),'yyyy-MM-dd,HH:mm:ss') }}</div>

						</div>
					</li>
				</ul>
			</div>

		</div>
	</div>
</template>

<script type='text/ecmascript-6'>
	import Star from '../star/star';
	import Split from '../split/split';
	import Ratingselect from '../ratingselect/ratingselect';
	// 引入时间格式化模块
	import DateFormat from 'format-datetime';
	import BScroll from 'better-scroll';

	const ALL = 2;
	const ERR_OK = 0;

	export default {
	  props: {
	    'seller': {
	      type: Object
	    }
	  },
	  data () {
	    return {
	      ratings: [],
	      selectType: ALL,
	      onlyContent: true,
	      desc: {
	        all: '全部',
	        positive: '推荐',
	        negative: '吐槽'
	      }
	    };
	  },
	  methods: {
	    needShow (type, text) {
	      if (this.onlyContent && !text) {
	        return false;
	      }
	      if (this.selectType === ALL) {
	        return true;
	      } else {
	        return type === this.selectType;
	      }
	    },
	    DateFormat,
	    _initscroll (el) {
	      this.$nextTick(() => {
	        this.scroll = new BScroll(el, {
	          click: true
	        });
	      });
	    },
	    ratingtypeSelect (type) {
	      this.selectType = type;
	      this.$nextTick(() => {
	        this.scroll.refresh();
	      });
	    },
	    toggleContent (onlyContent) {
	      this.onlyContent = onlyContent;
	      this.$nextTick(() => {
	        this.scroll.refresh();
	      });
	    }
	  },
	  created () {
	    this.$http.get('/api/ratings').then((res) => {
	      if (ERR_OK === res.data.errno) {
	        this.ratings = res.data.data;
	        // console.log(this.ratings);
	        // 调用初始化的方法
	        this._initscroll(this.$refs.ratingscroll);
	      }
	    });

	    // 监听事件
	    this.$root.eventHub.$on('ratingtype.select', this.ratingtypeSelect);
	    this.$root.eventHub.$on('content.toggleContent', this.toggleContent);
	  },
	  components: {
	    star: Star,
	    split: Split,
	    ratingselect: Ratingselect
	  }
	};
</script>

<style lang='stylus' rel='stylesheet/stylus'>
	@import '../../common/stylus/mixin.styl'

	.ratings
		position: absolute
		top: 174px
		bottom: 0
		left: 0
		width: 100%
		overflow: hidden
		.overview
			display: flex
			padding: 18px 0
			.overview-left
				flex: 0 0 138px
				width: 138px
				border-right: 1px solid rgba(7,17,27,0.6)
				text-align: center
				padding-bottom: 6px
				@media only screen and (max-width: 320px)
					flex: 0 110px
					width: 110px
				.score
					margin-bottom: 6px
					line-height: 28px
					font-size: 24px
					color: rgb(255,153,0) 
				.title
					margin-bottom: 8px
					line-height: 12px
					font-size: 12px
					color: rgb(7,17,27)
				.rank
					line-height: 10px
					font-size: 10px
					color: rgb(147,153,159)

			.overview-right
				flex: 1
				padding 6px 0 6px 14px
				@media only screen and (max-width: 320px)
					padding-left: 4px
				.score-wrapper
					margin-bottom 8px
					font-size: 0
					.title
						font-size 12px
						color rgb(7,17,27)
						display: inline-block
						line-height 18px
					.star
						display: inline-block
						margin: 0 12px
						vertical-align: top
					.score
						display: inline-block
						font-size: 12px
						line-height: 18px
						color: rgb(255,153,0)
						vertical-align: top
				.deliveryWrapper
					font-size: 0
					.title
						font-size: 12px
						color: rgb(7,17,27)
						line-height: 18px
					.deliverytime
						font-size: 12px
						color: rgb(147,153,159)
						margin-left: 10px
		.rating-wrapper
			padding: 0 18px
			.rating-item
				display: flex
				padding: 18px 0
				border-1px(rgba(7,17,27,0.1)) 
				.avatar
					flex: 0 0 28px
					width: 28px
					margin-right: 12px
					img
						border-radius: 50%
				.content
					position: relative
					flex: 1
					.name
						margin-bottom: 4px
						line-height: 12px
						font-size: 10px
						color: rbg(7,17,27)
					.star-wrapper
						margin-bottom: 6px
						font-size: 0
						overflow: hidden
						.star
							float: left
							margin-right: 6px
							vertical-align: top
							text-align: left
							margin-top: 10px
						.delivery
							float: left
							vertical-align: top
							font-size: 9px
							margin-top: 10px
							margin-left: 5px
							font-weight: 200
							color: rgb(147,153,159)
					.text
						margin-bottom: 8px
						line-height: 18px
						color: rgb(7,17,27)
						font-size: 12px
					.recommend
						line-height: 16px
						font-size: 0
						.icon-thumb_up,.item
							display: inline-block
							margin: 0 8px 4px 0
							font-size: 9px
						.icon-thumb_up
							color: rgb(0,160,220)
						.item
							padding: 0 6px
							border: 1px solid rgba(7,17,27,0.5)
							border-radius: 1px
							color: rgb(147,153,159)
							background: #fff
					.time
						position: absolute
						top: 0
						right: 0
						color: rgb(147,153,159)
</style>	


