<template>
	<div class='ratingselect'>
		<div class="rating-type border-1px">
			<span class='block positive' :class="{'active':curSelectType===2}" @click='select(2,$event)'>{{ desc.all }}<span class='count'>{{ ratings.length }}</span></span>
			<span class='block positive' :class="{'active':curSelectType===0}" @click='select(0,$event)'>{{ desc.positive }}<span class='count'>{{ positives.length }}</span></span>
			<span class='block negative' :class="{'active':curSelectType===1}" @click='select(1,$event)'>{{ desc.negative }}<span class='count'>{{ negatives.length }}</span></span>
		</div>
		<div class="switch" :class="{'on':curOnlyContent===true}">
			<span class="icon-check_circle" @click='toggleContent'></span>
			<span class="text">只看有内容的评价</span>
		</div> 
	</div>
</template>

<script type='text/ecmascript-6'>
	const POSITIVE = 0;
	const NEGATIVE = 1;
	const ALL = 2;
	export default {
	  props: {
	    ratings: {
	      type: Array,
	      default () {
	        return [];
	      }
	    },
	    selectType: {
	      type: Number,
	      default: ALL
	    },
	    onlyContent: {
	      type: Boolean,
	      default: false
	    },
	    desc: {
	      type: Object,
	      default () {
	        return {
	          all: '全部',
	          positive: '满意',
	          negative: '不满意'
	        };
	      }
	    }
	  },
	  data () {
	    return {
	      'curSelectType': null,
	      'curOnlyContent': null
	    };
	  },
	  methods: {
	    select (type, event) {
	      if (!event._constructed) {
	        // 如果不是自身点击事件派发的就返回false
	        return;
	      }
	      this.curSelectType = type;
	      // 然后派发一个事件
	      this.$root.eventHub.$emit('ratingtype.select', type);
	    },
	    toggleContent () {
	      if (!event._constructed) {
	        // 如果不是自身点击事件派发的就返回false
	        return;
	      }
	      this.curOnlyContent = !this.curOnlyContent;
	      // console.log(this.curOnlyContent);
	      this.$root.eventHub.$emit('content.toggleContent', this.curOnlyContent);
	    }
	  },
	  // 钩子函数 在创建对象的时候 把props的值传递给data prop的值不可以在子组件直接更改
	  created () {
	    this.curSelectType = this.selectType;
	    this.curOnlyContent = this.onlyContent;
	  },
	  computed: {
	    positives () {
	      var newArray = [];
	      this.ratings.forEach((val) => {
	        if (val.rateType === POSITIVE) {
	          newArray.push(val);
	        }
	      });
	      return newArray;
	    },
	    negatives () {
	      var newArray = [];
	      this.ratings.forEach((val) => {
	        if (val.rateType === NEGATIVE) {
	          newArray.push(val);
	        }
	      });
	      return newArray;
	    }
	  }
	};
</script>

<style lang='stylus' rel='stylesheet/stylus'>
	@import '../../common/stylus/mixin.styl'
	.ratingselect 
		.rating-type
			padding: 18px 0
			margin: 0 18px
			border-lpx(rgba(7, 17, 27, 0.1))
			font-size: 0
			.block
				display: inline-block
				padding: 8px 12px
				margin-right: 8px
				line-height: 16px
				border-radius: lpx
				font-size: 12px
				color: rgb(77, 85, 93)
				&.active
					color: #fff
				.count
					margin-left: 2px
					font-size: 8px
				&.positive
					background: rgba(0, 160, 220, 0.2)
					&.active
						background: rgb(0,160,220)
				&.negative
					background: rgba(77, 85, 93, 0.2)
					&.active
						background: rgb(77, 85, 93)
		.switch
			padding: 12px 18px
			line-height: 24px
			border-bottom: 1px solid rgba(7,17,27,0.1)
			color: rgb(147,153,159)
			font-size: 0
			&.on
				.icon-check_circle
					color: #00c850
			.icon-check_circle
				display: inline-block
				vertical-align: top
				margin-right: 4px
				font-size: 24px
			.text
				display: inline-block
				vertical-align: top
				font-size: 12px
							
</style>


